import { Component, OnInit, Input, ViewChild, ElementRef, OnChanges, OnDestroy } from '@angular/core';
import { WorkItemsService, ProcessMetadata } from '../../shared-services/work-items/work-items.service';
import { ActivatedRoute } from '@angular/router';

declare const moment: any;

@Component({
  selector: 'app-work-item-metadata',
  templateUrl: './work-item-metadata.component.html',
  styleUrls: ['./work-item-metadata.component.scss']
})
export class WorkItemMetadataComponent implements OnInit, OnChanges, OnDestroy {
  @Input('data') public parentData;
  @ViewChild('datePlaceholder') public datePlaceholder: ElementRef;

  @Input('metadata') public metadataItems;
  @Input('showFilterMenu') public showFilterMenu: boolean;
  public ddSelected: any;

  public metadata: ProcessMetadata;
  public showcustomBox = false;
  selectedTime: any;
  customactive: any;

  public errorMessage: string;
  public daterange: any = {};
  public isLoading: boolean = false;
  fromDate: any;
  toDate: any;
  customDateSelected: boolean;

  public options: any = {
    locale: { format: 'YYYY-MM-DD' },
    alwaysShowCalendars: false,
    ranges: {
      'Last Month': [moment().subtract(1, 'month'), moment()],
      'Last 3 Months': [moment().subtract(4, 'month'), moment()],
      'Last 6 Months': [moment().subtract(6, 'month'), moment()],
      'Last 12 Months': [moment().subtract(12, 'month'), moment()],
    }
  };
  sendObj: {};
  selectedTimeobj: {};


  constructor(
    public _workItemService: WorkItemsService , private route : ActivatedRoute) { 
     
  }

  ngOnInit() {
    console.log(this.route)
    // set only for the first time when @Input sends it...
    this.metadata = this.metadataItems;
    this.selectedDate = this._workItemService.selectedDate;
    console.log(this.selectedDate);

    if (this.selectedDate) {
      if (this.selectedDate["DURATION"]) {
        this.sendObj = {
          "DURATION": { "timeline": this.selectedDate["DURATION"].timeline, "value": this.selectedDate["DURATION"].value }
        }
        this.ddSelected = this.selectedDate["DURATION"].value;

      }
      else {
        this.sendObj = {
          "CUSTOM": { "start": this.selectedDate["CUSTOM"].start, "end": this.selectedDate["CUSTOM"].end }
        }
        this.customactive = 0;
      }
    }
    else {
      this.sendObj = {
        "DURATION": { "timeline": "days", "value": -1 }
      }
      this.ddSelected = -1;
      this.getWorkItemData();
    }
    this.getWorkItemData();


  }

  ngOnChanges(e) {
    this.metadata = this.metadataItems;
  }

  ngOnDestroy() {
  }

  showCustomdate(value) {
    this.customactive = value;
    if (this.selectedTime) {
      this.customDateSelected = true;
    }
    this.ddSelected = -2;
    this.customactive = value;
    this.showcustomBox = !this.showcustomBox;
  }

  closeCustomBox() {
    this.showcustomBox = false;
    this.fromDate = '';
    this.toDate = '';
  }

  getWorkItemData() {
    this.isLoading = true;
    console.log(this.sendObj);
    this._workItemService.getActiveWorkItemsByProcessName(this.route.snapshot.params["processId"], this.sendObj).subscribe((data: any) => {
      console.log("wmd"+data);
      // this._workItemService.sendactiveworkitemsObj = data.result;
      this._workItemService.sendactiveworkitemsObj.next(data.result["active_work_items"]);
      this.isLoading = false;
      this.metadata = data;
    }, (error) => {
      this.isLoading = false;
      this.errorMessage = error.error;
    });
  }


  public selectedDate(value: any, datepicker?: any) {
    this.options = {
      locale: { format: 'YYYY-MM-DD' },
      alwaysShowCalendars: false,
      ranges: {
        'Last Month': [moment().subtract(1, 'month'), moment()],
        'Last 3 Months': [moment().subtract(4, 'month'), moment()],
        'Last 6 Months': [moment().subtract(6, 'month'), moment()],
        'Last 12 Months': [moment().subtract(12, 'month'), moment()],
      }
    };

    this.ddSelected = 'Range';

    this.daterange = {};
    // or manupulat your own internal property
    this.daterange.start = value.start.format('YYYY-MM-DD');
    this.daterange.end = value.end.format('YYYY-MM-DD');


    this.getWorkItemData();
  }

  showDatePicker() {
    this.datePlaceholder.nativeElement.click();
  }

  // public showDatewiseFilter(type,value) {
  //   this.ddSelected = value;

  //   this.daterange = null;

  //   this.getWorkItemData();
  // }

  private apiDDType(): string {
    let typ: string = '';
    switch (this.ddSelected) {
      case 'YTD':
        typ = 'YEAR';
        break;
      case 'MTD':
        typ = 'MONTH';
        break;
      case 'WTD':
        typ = 'WEEK';
        break;
      case 'Range':
        typ = 'CUSTOM';
        break;
      case 'ALL':
        typ = 'ALL';
        break;
    }

    return typ;
  }

  formatDate(dateObj) {
    var month = dateObj.getMonth() + 1; //months from 1-12
    var day = dateObj.getDate();
    var year = dateObj.getFullYear();
    var hours = dateObj.getHours();
    var minutes = dateObj.getMinutes();
    var seconds = dateObj.getSeconds();

    return year + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds;
  }

  sendDate(fromDate, toDate) {
    this.customactive = 0;
    let claimtype = this._workItemService.selectedClaimTypes;
    if (this.customDateSelected) {
      if (this.selectedTime) {
        let fDate = this.formatDate(fromDate);
        let tDate = this.formatDate(toDate);
        this.selectedTimeobj = { "CUSTOM": { "start": fDate, "end": tDate } }
      }
    }
    else if (this.selectedTime) {
      let fDate = fromDate;
      let tDate = toDate;
      this.selectedTimeobj = { "CUSTOM": { "start": fDate, "end": tDate } };
    }
    else {
      let fDate = this.formatDate(fromDate);
      let tDate = this.formatDate(toDate);
      this.selectedTimeobj = { "CUSTOM": { "start": fDate, "end": tDate } };
    }
    this._workItemService.selectedDate = '';
    this._workItemService.selectedDate = this.selectedTimeobj;
    console.log(this.selectedTimeobj);
    this.showcustomBox = false;
    this._workItemService.getActiveWorkItemsByProcessName(this.route.snapshot.params["processId"], this.selectedTimeobj).subscribe((data: any) => {
      this._workItemService.sendactiveworkitemsObj.next(data.result["active_work_items"]);
            this.isLoading = false;
      this.metadata = data;
    }, (error) => {
      this.isLoading = false;
      this.errorMessage = error.error;
    });
  }

  showDatewiseFilter(days, value) {
    this.closeCustomBox();
    this.customactive = -3;
    this.ddSelected = value;
    // this.getWorkItemData();
    let obj = { "DURATION": { "timeline": days, "value": value } };
    this.isLoading = true;
    console.log(obj);
    this._workItemService.getActiveWorkItemsByProcessName(this.route.snapshot.params["processId"], obj).subscribe((data: any) => {
      this._workItemService.sendactiveworkitemsObj.next(data.result["active_work_items"]);
      this.isLoading = false;
      this.metadata = data;
    }, (error) => {
      this.isLoading = false;
      this.errorMessage = error.error;
    });
    setTimeout(()=>{
      this._workItemService.getactiveworkitemsObj.subscribe((data)=>{
        console.log(data);
      })
    },10000)
  }
}



