import { Pipe, PipeTransform } from '@angular/core';
@Pipe({
  name: 'keyvalue'
})
export class ObjectConvert implements PipeTransform {
  transform(dict: Object) {
    var a = [];
    for (var key in dict) {
      if (dict.hasOwnProperty(key)) {
        a.push({key: key, val: dict[key]});
      }
    }
    return a;
  }
}


