import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import {CustomMaterialModule} from './custom-material/custom-material.module';
import { FilterPipe } from './pipes/filter-pipe';
import { ProcessFilterPipe } from './pipes/processname-filter-pipe';
import { CustomPropsProvider } from './props-provider/CustomPropsProvider';
import { AdminService } from '../shared-services/admin/admin.service';
import { ProcessesFilterPipe } from './pipes/proc.filter';
import { ObjectConvert } from './pipes/objconvert';
import { SafePipe } from './pipes/dialog-content';
import { SlicePipe } from './pipes/slice.pipe';
import { OwlNativeDateTimeModule, OwlDateTimeModule } from 'ng-pick-datetime';
import { FormatCountPipe } from './pipes/format-count';


@NgModule({
  imports: [
    CommonModule,
    CustomMaterialModule,OwlNativeDateTimeModule,OwlDateTimeModule
  ],
  exports: [
    CustomMaterialModule,
    FilterPipe,
    ProcessFilterPipe,
    ProcessesFilterPipe,
    FormatCountPipe,
    ObjectConvert,SafePipe,OwlDateTimeModule,OwlNativeDateTimeModule
  ],
  providers: [
    AdminService
  ],
  declarations: [FilterPipe, SafePipe,ProcessFilterPipe, ProcessesFilterPipe,ObjectConvert, SlicePipe,FormatCountPipe]
 
})
export class SharedModulesModule { }



